.button-wrap {
  display: block;
  width: 100%;

  &__btn {
    padding: 8px 16px;
    border-radius: 2px;
    background: $main-gradient;
    background-size: 150% auto;
    transition: .5s;
    text-align: center;
    display: block;
    color: #fff;
    border: 0;
    text-decoration: none;
    cursor: pointer;
    font-family: inherit;
    font-size: 18px;
    line-height: 24px;
    font-style: normal;
    font-weight: 500;
    min-width: 180px;
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    margin: 0 auto;

    &:hover,
    &:focus {
      background-position: right center; } } }
